.about_container{
    display: grid;
    grid-template-columns:40% 50%;
    gap:10%;

}

 .about-me{
    width: 100%; 
    aspect-ratio: 1/1; 
    border-radius: 2rem;
    background: linear-gradient( -90deg,transparent,var(--color-primary));
    
   /* display: grid;  */
   /* place-items:center; */

}
.my-image{
    border-radius: 2rem;
    overflow:hidden;
    transform:rotate(10deg);
    transition:var(--transition);
}

.my-image:hover{
    transform:rotate(0deg);
}

.about_cards{
    display: grid;
    grid-template-columns: 33% 33% 34%;
    gap: 1rem;
}
 .about_card{
    background: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 1rem;
    padding:1rem;
    text-align: center;
 }
 .about_card:hover{
    background: transparent;
    border-color: var(--color-primary-variant);
    cursor:default;
 }
 .about_icon{
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;

 }
 .about_card h5{
    font-size: 1rem;
 }
 .about_card small{
     font-size: 0.rem;
     color:var(--color-light);
 }

 .about_content p{
    margin: 2rem 0 2.6rem;
    color:var(--color-light);
 }

 @media screen and (max-width:1024px){
   .about_container{
      grid-template-columns:1fr;
   }
   .about-me{
      width:50%;;
      margin:2rem auto 2rem;
   }
   .about_cards{
      grid-template-columns: 1fr 1fr 1fr;
      gap:1rem;
   }
   .about_content p{
      margin:1rem 0 1rem;
   }

  
 }


@media screen and (max-width:600px){
   /* .about_container{
      grid-template-columns:1fr;
   } */
   .about-me{
      width:50%;
      margin: 0 auto 3rem;
   }
   .about_cards{
      grid-template-columns:1fr 1fr;
      margin-top:1rem;
   } 

   .about_content{
      text-align: center;
   }
   .about_content p{
      margin:1.5rem 0 ;
   }
}
